.hero-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: insert 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  position: relative;
  font-weight: bold;
}

.btn-hero {
  position: absolute;
  z-index: 10;
  transform: translate(-50%, -50%);
  top: 60%;
  left: 50%;
  padding-left: 3%;
  padding-right: 3%;
  transition: 0.3s;
}

.btn-hero:hover {
  position: absolute;
  z-index: 10;
  transform: translate(-50%, -50%);
  top: 60%;
  left: 50%;
  padding-left: 5%;
  padding-right: 5%;
  opacity: 0.9;
}

.sub-title {
  font-weight: bold;
  position: absolute;
  z-index: 10;
  transform: translate(-50%, -50%);
  top: 35%;
  left: 50%;
}

.title {
  font-weight: bold;
  position: absolute;
  z-index: 10;
  transform: translate(-50%, -50%);
  top: 43%;
  left: 50%;
  font-size: xx-large;
}

.line {
  width: 5%;
  max-width: 50px;
  border-bottom: 2px solid white;
  z-index: 10;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 37%;
  left: 48%;
  animation: animate 1.5s linear infinite;
}

@keyframes animate {
  0% {
    left: 48%;
  }

  50% {
    left: 52%;
  }

  0% {
    left: 48%;
  }
}
