.title-detail-section {
  font-weight: bold;
  text-align: center;
  align-items: center;
}

.filler-detail-section {
  text-align: center;
  align-items: center;
}

.custom-jumbotron {
  align-items: center;
}

.custom-jumbotron::after {
  width: 100%;
  content: "";
  display: table;
  clear: both;
}

.custom-jumbotron-container {
  width: 45%;
  float: left;
  padding: 1%;
}

@media screen and (max-width: 1300px) {
  .custom-jumbotron-container {
    width: 100%;
    clear: both;
    float: none;
  }
}
